import Papa from 'papaparse';
import { DATA_SOURCES } from '../config';

const processProduct = (product) => ({
  ...product,
  id: product.id.toString(),
  imageUrls: Array.isArray(product.imageUrls) ? product.imageUrls : product.imageUrls.split(','),
  keywords: Array.isArray(product.keywords) ? product.keywords : product.keywords.split(',').map(k => k.trim())
});

const fetchJson = async (url, callback) => {
  try {
    const response = await fetch(url);
    const data = await response.json();
    callback(data.map(processProduct));
  } catch (error) {
    console.error(`Error fetching JSON from ${url}:`, error);
    callback([]);
  }
};

const fetchCsv = async (url, callback) => {
  try {
    const response = await fetch(url);
    const csvData = await response.text();
    const parsedData = Papa.parse(csvData, { header: true }).data;
    callback(parsedData.map(processProduct));
  } catch (error) {
    console.error(`Error fetching CSV from ${url}:`, error);
    callback([]);
  }
};

export const fetchProducts = (callback) => {
  let fetchedCount = 0;
  const totalSources = DATA_SOURCES.json.length + DATA_SOURCES.csv.length;

  const checkCompletion = () => {
    fetchedCount++;
    if (fetchedCount === totalSources) {
      callback(null); // Signals that all sources have been fetched
    }
  };

  DATA_SOURCES.json.forEach(url => {
    fetchJson(url, (products) => {
      callback(products);
      checkCompletion();
    });
  });

  DATA_SOURCES.csv.forEach(url => {
    fetchCsv(url, (products) => {
      callback(products);
      checkCompletion();
    });
  });
};

export const fetchProductById = async (id) => {
  return new Promise((resolve) => {
    let foundProduct = null;
    fetchProducts((products) => {
      if (products === null) {
        resolve(foundProduct);
      } else {
        const product = products.find(p => p.id === id.toString());
        if (product) {
          foundProduct = product;
        }
      }
    });
  });
};